"use client";

import { useState, useEffect, useRef, useLayoutEffect } from "react";
import io from "socket.io-client";
import { useRouter } from "next/navigation";
import { AppSidebar } from "@/components/sidebarBlazr";
import {
  ResizableHandle,
  ResizablePanel,
  ResizablePanelGroup,
} from "@/components/ui/resizable";
import { Plus, Search } from "lucide-react";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogFooter,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";

const API_URL = "http://localhost:3001";

interface Message {
  id: string;
  text: string;
  sender: string;
  timestamp: string;
  isRead: boolean;
  color?: string;
}

interface Participant {
  id: string;
  username: string;
}

interface Chat {
  id: string;
  name: string;
  participants: Participant[];
  lastMessage?: {
    content: string;
    timestamp: string;
    sender: string;
  };
}

interface User {
  id: string;
  username: string;
  color: string;
}

export default function Inbox() {
  const router = useRouter();
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const [messages, setMessages] = useState<Message[]>([]);
  const [chats, setChats] = useState<Chat[]>([]);
  const [selectedChat, setSelectedChat] = useState<Chat | null>(null);
  const [newMessage, setNewMessage] = useState("");
  const [newChatName, setNewChatName] = useState("");
  const [newChatParticipants, setNewChatParticipants] = useState("");
  const [open, setOpen] = useState(false);
  const [searchTerm, setSearchTerm] = useState("");

  const socketRef = useRef<ReturnType<typeof io> | null>(null);
  const sendSoundRef = useRef<HTMLAudioElement | null>(null);
  const receiveSoundRef = useRef<HTMLAudioElement | null>(null);
  const messagesEndRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    const fetchUser = async () => {
      try {
        const res = await fetch(`${API_URL}/users/me`, {
          credentials: "include",
        });
        if (res.ok) {
          const data = await res.json();
          setUser(data);
          setLoading(false);
        } else {
          router.push("/");
        }
      } catch {
        router.push("/");
      }
    };
    fetchUser();
  }, [router]);

  useEffect(() => {
    if (!user) return;
    const fetchChats = async () => {
      const res = await fetch(`${API_URL}/chats/user/me`, {
        credentials: "include",
      });
      if (res.ok) {
        const data = await res.json();
        const sortedChats = data.sort((a: Chat, b: Chat) => {
          const timeA = a.lastMessage?.timestamp ? new Date(a.lastMessage.timestamp).getTime() : 0;
          const timeB = b.lastMessage?.timestamp ? new Date(b.lastMessage.timestamp).getTime() : 0;
          return timeB - timeA;
        });
        setChats(sortedChats);
      }
    };
    fetchChats();
  }, [user]);

  useEffect(() => {
    if (!selectedChat) return;
    const fetchMessages = async () => {
      const res = await fetch(`${API_URL}/messages/chat/${selectedChat.id}`, {
        credentials: "include",
      });
      if (res.ok) {
        const data = await res.json();
        const formattedMessages = data.map((msg: any) => ({
          id: msg.id.toString(),
          text: msg.content,
          sender: msg.sender?.username || "unknown",
          timestamp: msg.timestamp,
          isRead: msg.isRead,
          color: msg.sender.color,
        }));
        setMessages(formattedMessages);
      }
    };
    const markAsRead = async () => {
      await fetch(`${API_URL}/messages/markAsRead/${selectedChat.id}`, {
        method: "PATCH",
        credentials: "include",
      });
    };
    fetchMessages();
    markAsRead();
  }, [selectedChat]);

  useEffect(() => {
    if (!selectedChat || !user) return;

    socketRef.current = io(API_URL, {
      transports: ["polling", "websocket"],
    });

    const socket = socketRef.current;
    socket.emit("joinChat", {
      chatId: selectedChat.id,
      userId: user.id,
    });

    socket.on("newMessage", (message: any) => {
      if (message.sender.username === user.username) return;

      const newMsg = {
        id: message.id.toString(),
        text: message.content,
        sender: message.sender.username,
        timestamp: message.timestamp,
        isRead: message.isRead,
        color: message.sender.color,
      };

      setMessages((prev) => [...prev, newMsg]);
      setChats((prevChats) => {
        const updatedChats = prevChats.map((chat) =>
          chat.id === selectedChat.id
            ? {
                ...chat,
                lastMessage: {
                  content: message.content,
                  timestamp: message.timestamp,
                  sender: message.sender.username,
                },
              }
            : chat
        );
        return updatedChats.sort((a, b) => {
          const timeA = a.lastMessage?.timestamp ? new Date(a.lastMessage.timestamp).getTime() : 0;
          const timeB = b.lastMessage?.timestamp ? new Date(b.lastMessage.timestamp).getTime() : 0;
          return timeB - timeA;
        });
      });

      receiveSoundRef.current?.play();
      fetch(`${API_URL}/messages/markAsRead/${selectedChat.id}`, {
        method: "PATCH",
        credentials: "include",
      });
    });

    return () => {
      socket.disconnect();
      socketRef.current = null;
    };
  }, [selectedChat, user]);

  useLayoutEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const handleSend = () => {
    if (newMessage.trim() === "" || !socketRef.current || !selectedChat || !user) return;

    const tempId = Date.now().toString();
    const timestamp = new Date().toISOString();

    socketRef.current.emit("sendMessage", {
      chatId: selectedChat.id,
      userId: user.id,
      content: newMessage,
    });

    const sentMessage = {
      id: tempId,
      text: newMessage,
      sender: user.username,
      timestamp,
      isRead: false,
      color: user.color,
    };

    setMessages((prev) => [...prev, sentMessage]);
    setChats((prevChats) => {
      const updatedChats = prevChats.map((chat) =>
        chat.id === selectedChat.id
          ? {
              ...chat,
              lastMessage: {
                content: newMessage,
                timestamp,
                sender: user.username,
              },
            }
          : chat
      );
      return updatedChats.sort((a, b) => {
        const timeA = a.lastMessage?.timestamp ? new Date(a.lastMessage.timestamp).getTime() : 0;
        const timeB = b.lastMessage?.timestamp ? new Date(b.lastMessage.timestamp).getTime() : 0;
        return timeB - timeA;
      });
    });

    sendSoundRef.current?.play();
    setNewMessage("");
  };

  const handleCreateChat = async () => {
    if (!newChatName.trim()) return;
    const res = await fetch(`${API_URL}/chats`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      credentials: "include",
      body: JSON.stringify({
        name: newChatName.trim(),
        participantUsernames: newChatParticipants
          .split(",")
          .map((u) => u.trim())
          .filter((u) => u.length > 0),
      }),
    });

    if (!res.ok) return;

    const data = await res.json();
    setChats((prev) => [data, ...prev]);
    setNewChatName("");
    setNewChatParticipants("");
    setOpen(false);
  };

  const getOtherParticipants = (chat: Chat) =>
    chat.participants.filter((p) => p.username !== user?.username);

  const filteredChats = chats.filter((chat) => {
    const otherNames = getOtherParticipants(chat)
      .map((p) => p.username.toLowerCase())
      .join(" ");
    return (
      chat.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      otherNames.includes(searchTerm.toLowerCase())
    );
  });

  const formatTime = (timestamp: string) => {
    const date = new Date(timestamp);
    const now = new Date();
    const diffInHours = (now.getTime() - date.getTime()) / (1000 * 60 * 60);

    if (diffInHours < 24) {
      return date.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" });
    } else if (diffInHours < 48) {
      return "Hier";
    } else {
      return date.toLocaleDateString([], { day: "2-digit", month: "2-digit" });
    }
  };

  const truncateMessage = (message: string, maxLength = 50) =>
    message.length > maxLength ? `${message.substring(0, maxLength)}...` : message;

  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen text-gray-500 text-lg">
        Chargement...
      </div>
    );
  }

  return (
    <div className="flex flex-col sm:flex-row h-screen overflow-hidden bg-gray-100 text-gray-900">
      <audio ref={sendSoundRef} src="/send.mp3" preload="auto" />
      <audio ref={receiveSoundRef} src="/receive.mp3" preload="auto" />

      <div className="w-full sm:w-16 shrink-0 bg-white border-b sm:border-r sm:border-b-0">
        <AppSidebar />
      </div>

      <ResizablePanelGroup direction="horizontal" className="flex-1 flex h-full">
        <ResizablePanel defaultSize={30} minSize={20} className="flex flex-col bg-white border-r shadow-sm">
          
        </ResizablePanel>

        <ResizableHandle className="bg-gray-200 hover:bg-indigo-300 cursor-col-resize" />

        <ResizablePanel defaultSize={70} minSize={40} className="flex flex-col bg-white">
          
        </ResizablePanel>
      </ResizablePanelGroup>
    </div>
  );
}
